import { CLEAR_SESSION, SET_SESSION_TABS, SET_TAB_PLOTS} from '../actions/actionTypes'
import _ from "lodash"

const defaultState = []

const tabs = (state = defaultState, action) => {

    switch (action.type) {
        case SET_SESSION_TABS:
            console.log("REDUX: Setting session tabs...")
            var newState = action.tabs
            console.log(newState)

            return newState
        
        case SET_TAB_PLOTS:
            console.log("REDUX: Setting tab plots...")
            const iTab = _.findIndex(state, ["id", action.tabID])

            var newState = _.cloneDeep(state)

            newState[iTab] = {...state[iTab], plots: action.plots}
            
            console.log(newState)
            return newState

        case CLEAR_SESSION:
            return defaultState

        default:
            return state
    }
}

export default tabs