import { SET_PLOTABLES, CLEAR_SESSION } from '../actions/actionTypes'

const defaultState = {}

const plotables = (state = defaultState, action) => {
    switch (action.type) {
        case SET_PLOTABLES:
            console.log("REDUX: Setting the new plotables...")
            let newState = {
                ...action.newPlotables
            }
            console.log(newState)
            return newState


        default:
            return state
    }
}

export default plotables