import React, { Component } from 'react'
import { connect } from 'react-redux'

import {Row} from 'react-materialize'
import Select from 'react-select'

import _ from "lodash"
import PythonApi from '../../apis/PythonApi'

class PlotTypePicker extends Component {

    constructor(props){
        super(props)
        this.state={}
    }

    componentDidMount(){
        //Get the available plots
        PythonApi.getPlotOptions( plotOptions => this.setState({plotOptions}) )
    }

    render() {

        const value = _.find(this.state.plotOptions, ["value", this.props.value])

        return (
            <Select
                options={this.state.plotOptions}
                placeholder="Choose the plot type..."
                isClearable isSearchable isMulti
                onChange={(obj) => this.props.onChange(obj ? obj.value || obj.map(selected => selected.value) : undefined)}
                value={value}
            />
        )
    }
}

const mapStateToProps = state => ({
    tabs: state.tabs,
    active: state.active
})

const mapDispatchToProps = {

}

export default connect(mapStateToProps, null)(PlotTypePicker);